
import Vue from 'vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import dateFormat from 'vux/src/tools/date/format.js'

import { checkLoginToLogin } from '@/api/login'
import * as base from '@/api/base'
import _ from 'lodash'
import PullTo from 'vue-pull-to'
Vue.use(DatetimePlugin)
Vue.use(AjaxPlugin)
Vue.use(ToastPlugin)
export default {
name: 'stock-history',
components: {
XHeader,
Masker,
XButton,
FlexboxItem,
Flexbox,
Checker,
CheckerItem,
DatetimePlugin,
XInput,
Group,
Datetime,
PullTo,
dateFormat
},
data () {
return {
screenType: -1,
count: 0,
loadCount: 0,
page: 1,
checkDate: '',
notLoadAll: true, // 没有全部加载
typeName: ['进货', '奖励', '转入', '转出'],
actionName: ['进货', '奖励', '转入', '转出'],
screenDate: '',
list: [],
dataList: [],
stock: 0
}
},
methods: {
change (value) {
this.checkDate = value
this.screenDate = value
this.dataList = []
this.notLoadAll = true
this.fetchList(false, 'No')
},
fetchList (url, isLoad, loaded) {
let urlStr = '/api/v1/stocklist/?limit=5&type=' + this.screenType + '&page=' + this.page
if (this.checkDate) urlStr += '&date=' + this.checkDate
url = url || urlStr
isLoad = isLoad || 'No'
base.fetch(url, null, response => {
let data = response.data
let msg = data.msg || data.error || '请求失败'
if (data.code !== 0) {
this.$vux.toast.text(msg, 'middle')
return false
}

let list = data.data.list

if (isLoad === 'Yes') {
this.dataList = this.dataList.concat(list)
} else {
this.dataList = list
}
this.count = data.data.count
this.dataList.forEach((content) => {
content.createdAtDate = dateFormat(new Date(content.createdAt), 'YYYY-MM-DD HH:mm:ss')
content.createdAt = content.createdAt.substring(0, 10)
})

if (this.count === this.dataList.length) {
this.notLoadAll = false
this.$vux.toast.text('全部加载完成', 'middle')
} else {
this.notLoadAll = true
}
this.list = _.groupBy(this.dataList, function (n) {
return n.createdAt
})
if (isLoad === 'Yes') loaded('done')
}, error => {
console.log(error)
}, 'GET')
},
loadmore (loaded) {
let urlStr = '/api/v1/stocklist/?limit=5&type=' + this.screenType + '&page=' + (this.page + 1)
this.fetchList(urlStr, 'Yes', loaded)
},
stateChange (state) {
if (state === 'pull' || state === 'trigger') {
this.iconLink = '#icon-arrow-bottom'
} else if (state === 'loading') {
this.iconLink = '#icon-loading'
} else if (state === 'loaded-done') {
this.iconLink = '#icon-finish'
}
},
checkTypeAction (value, disabled) {
this.screenType = value
this.fetchList()
},
jumpTurnCard () {
this.$router.push('/index/customer')
}
},
created () {
checkLoginToLogin(this)
this.fetchList()
base.fetch('/api/v1/agents/userinfo', null, response => {
let data = response.data
this.stock = data.coin.stock
}, error => {
console.log(error)
}, 'GET')
}
}



